import React, { useState, useEffect } from 'react';
import Slider from 'rc-slider';

import './App.css';
import 'rc-slider/assets/index.css';

function App() {
  const [headDmg, setHeadDmg] = useState(0);
  const [bodyDmg, setBodyDmg] = useState(0);
  const [legsDmg, setLegsDmg] = useState(0);
  const [regStrike, setRegStrike] = useState(50);
  const [sigStrike, setSigStrike] = useState(100);
  const [recoveryRate, setRecoveryRate] = useState(1);
  const [recoveryInterval, setRecoveryInterval] = useState(100);

  useEffect(() => {
    var timerID = setInterval(() => updateDmg(), recoveryInterval);
    return () => {
        clearInterval(timerID);
      };
   });
   
  const updateDmg = () => {
    if (headDmg > 0) {
      setHeadDmg(dmg => dmg - recoveryRate > 0 ? dmg - recoveryRate : 0);
    }
    if (bodyDmg > 0) {
      setBodyDmg(dmg => dmg - recoveryRate > 0 ? dmg - recoveryRate : 0);
    }
    if (legsDmg > 0) {
      setLegsDmg(dmg => dmg - recoveryRate > 0 ? dmg - recoveryRate : 0);
    }
  };

  const addDmg = (part, strike) => {
    if (strike === 'reg') {
      if (part === 'head') {
        setHeadDmg((dmg) => dmg + regStrike);
      } else if (part === 'body') {
        setBodyDmg((dmg) => dmg + regStrike);
      } else if (part === 'legs') {
        setLegsDmg((dmg) => dmg + regStrike);
      }
    } else if (strike === 'sig') {
      if (part === 'head') {
        setHeadDmg((dmg) => dmg + sigStrike);
      } else if (part === 'body') {
        setBodyDmg((dmg) => dmg + sigStrike);
      } else if (part === 'legs') {
        setLegsDmg((dmg) => dmg + sigStrike);
      }
    }
  };

  return (
    <div className="heatmap_wrapper">
      <div className="heatmap_stats">
        <div className="stat">
          <div className="stat_label">Head</div>
          <div className="stat_value">{ headDmg }</div>
        </div>
        <div className="stat">
          <div className="stat_label">Body</div>
          <div className="stat_value">{ bodyDmg }</div>
        </div>
        <div className="stat">
          <div className="stat_label">Legs</div>
          <div className="stat_value">{ legsDmg }</div>
        </div>
      </div>
      <div className="heatmap_container">
        <div className="heatmap_fighter">
          <div className="heatmap_head" style={{ opacity: headDmg > 299 ? 1 : headDmg / 300 }} />
          <div className="heatmap_body" style={{ opacity: bodyDmg > 299 ? 1 : bodyDmg / 300 }} />
          <div className="heatmap_legs" style={{ opacity: legsDmg > 299 ? 1 : legsDmg / 300 }} />
        </div>
      </div>
      <div className="heatmap_buttons">
        <div className="reg_strike" onClick={() => addDmg('head', 'reg')}>Reg Head</div>
        <div className="reg_strike" onClick={() => addDmg('body', 'reg')}>Reg Body</div>
        <div className="reg_strike" onClick={() => addDmg('legs', 'reg')}>Reg Legs</div>
        <div className="sig_strike" onClick={() => addDmg('head', 'sig')}>Sig Head</div>
        <div className="sig_strike" onClick={() => addDmg('body', 'sig')}>Sig Body</div>
        <div className="sig_strike" onClick={() => addDmg('legs', 'sig')}>Sig Legs</div>
      </div>
      <div className="settings_wrapper">
        <div className="setting">
          <div className="setting_label">Regular Strike Damage: {regStrike}</div>
          <Slider min={0} max={150} step={10} value={regStrike} onChange={dmg => setRegStrike(dmg)}/>
        </div>
        <div className="setting">
          <div className="setting_label">Significant Strike Damage: {sigStrike}</div>
          <Slider min={0} max={250} step={10} value={sigStrike} onChange={dmg => setSigStrike(dmg)}/>
        </div>
        <div className="setting">
          <div className="setting_label">Recovery Rate: {recoveryRate}</div>
          <Slider min={0} max={10} step={1} value={recoveryRate} onChange={rate => setRecoveryRate(rate)}/>
        </div>
        <div className="setting">
          <div className="setting_label">Recovery Interval: {recoveryInterval}</div>
          <Slider min={0} max={500} step={50} value={recoveryInterval} onChange={val => setRecoveryInterval(val)}/>
        </div>
      </div>
    </div>
  );
}

export default App;
